.container {
    flex: 1 !important;
    padding: 24px;

    background-color: #F2F2F2;
}

.head_repository {
    text-align: start;
    margin: 0;
    margin-bottom: 16px;

    font-size: 32px;
    font-weight: 400;

    color: #000000DE;
}

.dsc_container {
    display: flex;

    .lng_and_dsc_container {

       flex: 2;
       display: flex;
       flex-direction: column;
       align-items: start;
       .language {

        margin: 0;
        padding: 7px 10px;

        font-weight: 400;
        font-size: 13px;

        color: white;
        background-color: #2196F3;
        
        border-radius: 100px;
       }
    }
    .stars_container {
        flex: 1;
        display: flex;
        align-items: flex-start;
        justify-content: flex-end;

        .star_svg {
            margin-right: 2px;
            fill: #FFB400;
        }

        .stars {
            display: flex;
            align-items: center;
        }
    }
}


.license {
    text-align: start;

    font-size: 14px;
    color: #000000DE;
}

.link {
    .svg_gth{
        height: 40px;
        width: 40px;
        fill: black;
    }
}

.topic_list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    gap: 8px;

    padding-inline-start: 0px;

    font-size: 13px;
    font-weight: 400;
    &__item {
        padding: 4px 10px;
        background-color: #00000014;
        border-radius: 100px;
    }
}
